header {
    width: 100%;
    text-align: center;
    background-color: #3C3D37;
    color: #ECDFCC;
    padding: 10px 5px;
}

header h1 {
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
}
header h1:hover {
    cursor: pointer;
}
header h1:active {
    opacity: 0.5;
}

footer {
    background-color: #3C3D37;
    width: 100%;
    padding: 10px 5px;
}

footer h2 {
    margin-bottom: 10px;
}

footer #c-and-a {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

footer #c-and-a div {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 5px;
    column-gap: 20px;
    width: min-content;
    white-space: nowrap;
}

footer p, footer h2 {
    color: #ECDFCC;
}
footer a:hover {
    cursor: pointer;
}

#copyright {
    margin-top: 20px;
    width: 100%;
    text-align: center;
}