* {
  padding: 0;
  margin: 0;
  font-family: sans-serif;
  box-sizing: border-box;
  color: #ECDFCC;
}

a {
    color: #d2b7e9;
    text-decoration: underline;
}
a:hover {
    cursor: pointer;
}
a:active {
    opacity: 0.5;
}

input {
    background-color: transparent;
    color: #ECDFCC;
    outline: none;
    border: 1px solid #ECDFCC;
    padding: 3px 5px;
    border-radius: 7px;
    width: 100%;
    margin: 5px 0 10px;
}
input::placeholder {
    opacity: 0.5;
    color: #ECDFCC;
}
input:focus {
    border: 2px solid #ECDFCC;
}

.clickable {
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
    padding: 2px 7px;
    background-color: #3C3D37;
    width: min-content;
    white-space: nowrap;
    border: #ECDFCC 1px solid;
    border-radius: 6px;
}
.clickable:hover {
    cursor: pointer;
}
.clickable:active {
    opacity: 0.5;
}

#root-child {
  min-height: 100dvh;
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-columns: minmax(0, 1fr);
}

.stam-far {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30%;
    margin: 10px auto;
}
@media (max-width: 1350px) {
    .stam-far {
        width: 40%;
    }
}
@media (max-width: 1050px) {
    .stam-far {
        width: 50%;
    }
}
@media (max-width: 767px) {
    .stam-far {
        width: 70%;
    }
}
@media (max-width: 450px) {
    .stam-far {
        width: 90%;
    }
}